'use client'

import { useState } from 'react';
import { motion } from 'framer-motion';
import { Mail, Lock, User, ArrowRight, ArrowLeft } from 'lucide-react';
import { useAuth } from '../../../src/context/AuthContext';
import Link from 'next/link';
import QuatrefoilBackground from '../../../src/components/ui/QuatrefoilBackground';
import SparticlesEffect from '../../../src/components/ui/SparticlesEffect';

const AdminLoginPage = () => {
    const [isLogin, setIsLogin] = useState(true);
    const [formData, setFormData] = useState({ username: '', email: '', password: '' });
    const [error, setError] = useState('');
    const { login, register } = useAuth();
    const [isLoading, setIsLoading] = useState(false);

    const handleChange = (e) => {
        setFormData({ ...formData, [e.target.name]: e.target.value });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setError('');
        setIsLoading(true);

        if (isLogin) {
            const result = await login(formData.email, formData.password);
            if (!result.success) setError(result.message);
        } else {
            const result = await register(formData.username, formData.email, formData.password);
            if (result.success) {
                setIsLogin(true);
                setError('Registration successful! Please login.');
            } else {
                setError(result.message);
            }
        }
        setIsLoading(false);
    };

    return (
        <div className="w-full min-h-screen relative overflow-hidden flex items-center justify-center">
            {/* 3D Backgrounds */}
            <div className="fixed inset-0 pointer-events-none z-0">
                <QuatrefoilBackground />
                <SparticlesEffect />
            </div>

            {/* Back to Home Action */}
            <Link href="/" className="absolute top-6 left-6 z-20 flex items-center gap-2 text-zinc-600 hover:text-zinc-900 transition-colors font-medium group">
                <ArrowLeft size={20} className="group-hover:-translate-x-1 transition-transform" />
                Back to Home
            </Link>

            <motion.div
                initial={{ opacity: 0, scale: 0.95 }}
                animate={{ opacity: 1, scale: 1 }}
                transition={{ duration: 0.5 }}
                className="w-full max-w-md bg-white/80 backdrop-blur-md border border-zinc-200 rounded-2xl p-8 shadow-xl z-10"
            >
                {/* Header */}
                <div className="text-center mb-8">
                    <img src="/logo.png" alt="Asia By Gram" className="h-20 mx-auto mb-4 object-contain drop-shadow-sm" />
                    <h1 className="text-2xl font-serif text-zinc-900 mb-2">Welcome to ASIA BY GRAM</h1>
                    <p className="text-zinc-500 text-sm">{isLogin ? 'Admin Dashboard Access' : 'Create Admin Account'}</p>
                </div>

                {/* Form */}
                <form onSubmit={handleSubmit} className="space-y-4">
                    {!isLogin && (
                        <div className="space-y-2">
                            <label className="text-xs uppercase tracking-wider text-zinc-500 font-semibold ml-1">Username</label>
                            <div className="flex items-center bg-zinc-50 border border-zinc-300 rounded-lg px-4 py-3 focus-within:border-primary focus-within:ring-1 focus-within:ring-primary/20 transition-all">
                                <User className="text-zinc-400" size={18} />
                                <input
                                    type="text"
                                    name="username"
                                    placeholder="Enter username"
                                    value={formData.username}
                                    onChange={handleChange}
                                    className="bg-transparent border-none outline-none text-zinc-900 ml-3 w-full placeholder-zinc-400"
                                    required={!isLogin}
                                />
                            </div>
                        </div>
                    )}

                    <div className="space-y-2">
                        <label className="text-xs uppercase tracking-wider text-zinc-500 font-semibold ml-1">Email Address</label>
                        <div className="flex items-center bg-zinc-50 border border-zinc-300 rounded-lg px-4 py-3 focus-within:border-primary focus-within:ring-1 focus-within:ring-primary/20 transition-all">
                            <Mail className="text-zinc-400" size={18} />
                            <input
                                type="email"
                                name="email"
                                placeholder="name@company.com"
                                value={formData.email}
                                onChange={handleChange}
                                className="bg-transparent border-none outline-none text-zinc-900 ml-3 w-full placeholder-zinc-400"
                                required
                            />
                        </div>
                    </div>

                    <div className="space-y-2">
                        <label className="text-xs uppercase tracking-wider text-zinc-500 font-semibold ml-1">Password</label>
                        <div className="flex items-center bg-zinc-50 border border-zinc-300 rounded-lg px-4 py-3 focus-within:border-primary focus-within:ring-1 focus-within:ring-primary/20 transition-all">
                            <Lock className="text-zinc-400" size={18} />
                            <input
                                type="password"
                                name="password"
                                placeholder="••••••••"
                                value={formData.password}
                                onChange={handleChange}
                                className="bg-transparent border-none outline-none text-zinc-900 ml-3 w-full placeholder-zinc-400"
                                required
                            />
                        </div>
                    </div>

                    {error && (
                        <p className="text-red-400 text-xs text-center">{error}</p>
                    )}

                    <button
                        type="submit"
                        disabled={isLoading}
                        className="w-full bg-primary hover:bg-primary/90 text-black font-bold py-3.5 rounded-lg flex items-center justify-center gap-2 transition-all mt-6"
                    >
                        {isLoading ? 'Processing...' : (isLogin ? 'Sign In' : 'Create Account')}
                        {!isLoading && <ArrowRight size={18} />}
                    </button>

                    <div className="relative my-6">
                        <div className="absolute inset-0 flex items-center">
                            <div className="w-full border-t border-zinc-200"></div>
                        </div>
                        <div className="relative flex justify-center text-xs uppercase">
                            <span className="bg-transparent px-2 text-zinc-400 backdrop-blur-sm">Or continue with</span>
                        </div>
                    </div>

                    <button
                        type="button"
                        className="w-full bg-white border border-zinc-200 text-zinc-700 font-semibold py-3 rounded-lg flex items-center justify-center gap-2 hover:bg-gray-50 transition-colors shadow-sm"
                        onClick={() => alert("Google Auth requires configured credentials.")}
                    >
                        <svg className="w-5 h-5" viewBox="0 0 24 24">
                            <path d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z" fill="#4285F4" />
                            <path d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z" fill="#34A853" />
                            <path d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z" fill="#FBBC05" />
                            <path d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z" fill="#EA4335" />
                        </svg>
                        Google
                    </button>

                </form>

                {/* Footer Switch */}
                <div className="mt-6 text-center">
                    <p className="text-zinc-500 text-sm">
                        {isLogin ? "Don't have an account?" : "Already have an account?"}
                        <button
                            onClick={() => setIsLogin(!isLogin)}
                            className="ml-2 text-primary hover:text-primary/80 transition-colors font-bold"
                        >
                            {isLogin ? 'Sign up' : 'Log in'}
                        </button>
                    </p>
                </div>
            </motion.div>
        </div>
    );
};

export default AdminLoginPage;
